.product-page {
    display: flex;
    justify-content: center;
    margin: 5vh auto;
    width: 80%;
    padding: 20px;
    border: 1px solid rgb(220, 220, 220);
    border-radius: 5px;
}

.product-page .product-image {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
}

.product-page .product-image img {
    width: 100%;
    max-width: 300px;
    vertical-align: middle;
}

.product-page .product-informations {
    width: 50%;
}

.product-page .product-name-brand-mobile {
    display: none;
}

.product-page .product-name-brand,
.product-page .product-name-brand-mobile {
    font-weight: 600;
    margin-bottom: 5px;
}

.product-page .product-name-brand span,
.product-page .product-name-brand-mobile span {
    font-weight: 400;
    font-size: clamp(1rem, 3vw, 1.5rem);
}

.product-page .product-description span,
.product-page .product-category span {
    font-weight: 600;
}

.product-page .product-category {
    margin-bottom: 10px;
}

.product-page .product-description {
    margin-bottom: 10px;
}

.product-page .product-price {
    font-size: clamp(1.2rem, 4vw, 2rem);
    margin-bottom: 5px;
}

.product-page .product-add {
    width: 100%;
    max-width: 200px;
    background-color: #30967E;
    border: none;
    color: #FFF;
    padding: calc(10 * var(--res));
    cursor: pointer;
    font-size: clamp(1rem, 2vw, 1.1rem);
    border-radius: 5px;
    transition: .3s;
}

.product-page .product-add:hover {
    background-color: #3BB296;
}

.product-page .product-quantity {
    width: 100%;
    max-width: 200px;
    margin-bottom: 20px;
}

.product-page .product-quantity label {
    font-weight: 600;
    padding-right: 10px;
}

.product-page .product-quantity input {
    width: 100%;
    max-width: 200px;
}

.product-page .product-price .promo {
    text-decoration: line-through;
}

.product-page .product-price .promo-value {
    padding-left: 15px;
    color:rgb(255, 59, 59);
}

.product-page .stock-empty-verif {
    font-size: clamp(0.8rem, 1vw, 1rem);
    color: rgb(220, 53, 69);
    margin-top: 10px;
}

@media screen and (max-width: 550px) {
    .product-page {
        flex-direction: column;
    }
    .product-page .product-image {
        width: 100%;
    }
    .product-page .product-name-brand-mobile {
        display: block;
        line-height: 1.2;
    }
    .product-page .product-name-brand {
        display: none;
    }
    .product-page .product-informations {
        width: 100%;
    }
}